* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #D91E18;
  color: white;
  font-family: Helvetica, Arial, sans-serif;
  text-align: center;
  padding: 50px;
}

.hidden {
  display: none;
}

.block {
  display: block;
}

.center {
  margin: 0 auto;
  text-align: center;
}

.symbol {
  fill: white;
}

input {
  border: 1px solid rgba(0, 0, 0, .4);
  padding: 10px;
}